import React from 'react'

const LoginForm = ({handleLogin, handleChangeUser, handleChangePassword, newUser, password}) => (
  <>
    <form onSubmit={handleLogin}>
      USERNAME
      <input type="text" value={newUser} onChange={handleChangeUser}/><br/>
      PASSWORD
      <input type="text" value={password} onChange={handleChangePassword}/><br/>
      <input type="submit" value="LOGIN" />
    </form>
  </>
)

export default LoginForm